#btt{
    position: relative;
    background-color: aquamarine;
    width: 110px;
    height: 40px;
    color: #1d2228;
}

#btt:active:before{
    box-shadow: inset 5px 5px 10px rgba(0,0,0,0.7);
    transition: 0.20s;
}

#btt:active{
    box-shadow: inset 5px 5px 10px rgba(0,0,0,0.7);
    transition: 0.20s;
}

        #btt:after{
            color:#1d2228;
            font-weight: bold;
              content: "join NewsLetter";
              position: absolute;
              left: 0;
              top: 0;
              bottom: -62px;
           
              transition: 0.10s;
              right: 0;
              width: fit-content;
              /* height: 100%; */
              border-radius: 10px;
            overflow: hidden;
              z-index: 5;
              text-align: center;
              padding-top: 10px;
              width: 110px;
              height: 40px;
          }
          
          #btt::before{
            color: white;
              content: "";
              position: absolute;
              text-align: center;
              left: 0px;
              top: 0;
              overflow: hidden;
          padding-top: 0px;
              bottom: 50px;
            
              right: 0;
              width: inherit;
              height: ;
              border-radius: 10px;
              transition: 0.90s ease;
              background-color:aqua;
              width: 110px;
           
          }
          
          
          #btt:hover::after{
          color: black
          }
          
