.Nav_container{
width: 100vw;
max-width: 100%;
min-width: 100%;

height: 50px;
display: flex;
color: #efefef;

position: fixed;
top: 0;
justify-content: space-around;
align-items: center;
justify-items: center;

background-color: #1f1e1e;
z-index: 9;

}

.b{
    color: blue;
    font-size: 1.4em;
    text-shadow:  1.2px 1.2px  2px  #1d06e9b2;
}

.blue{
    color: rgb(102, 102, 252);
    font-size: 1.4em;
    text-shadow:  1.2px 1.2px  2px  #1d06e9b2;

}

.Nav_container li{
    list-style-type: none;

    font-size: 1.5em;
    cursor: pointer;

}

.Nav_container li a{
    text-decoration: none;
   
    color: #efefef;

}


.Acc_Socials_container{
width: fit-content;
position: absolute;
display: flex;
padding: 10px;
left: 0;
font-size: 0.65rem;
top: 10px;
margin-left: 35px;
float: right;
}

.Acc_Socials_container li{
padding: 5px;
}

.Acc_Login_signup_container{
width: fit-content;
display: flex;
padding: 10px;

 position: absolute;
right: 0px;
top: -5px;
clear: left;
float: right;
margin-right: 10px;
}

.Acc_login_btn{
    font-weight: bold;
    font-size: 0.99em;
color: #efefef;
}

.Acc_login_btn:hover{
color: blue
}
.Acc_signup_btn{
    font-weight: 600;
    font-size: 0.99em;
}

.Acc_signup_btn:hover{
color: blue;
}

.Acc_Login_signup_container li{
    padding: 2px;
    }
    

    .fb:hover{
        background-color: white;
        color: #4267B2;
        border-radius: 3px;
        padding-top: 4px;
        }
        .fb{
            font-size: 1.3em;
        }
        .pin:hover{
            background-color: crimson;
            color: white;
            border-radius: 3px;
            padding-top: 4px;
        }
        .pin{
            font-size: 1.3em;
        }
        .linked:hover{
            background-color: rgb(20, 67, 220);
            color: white;
            border-radius: 3px;
            padding-top: 4px;
        }
        .linked{
            font-size: 1.3em;
        }
        .twit:hover{
            background-color: rgb(255, 255, 255);
            color: rgb(18, 82, 219);
            border-radius: 3px;
            padding-top: 4px;
        }

        .twit{
            font-size: 1.3em;
        }


        @media  screen and (max-width: 399px){
            
            .Acc_Socials_container{
           display: none;
            }
            .Nav_container li{
font-size: 1em;
}
    
        }



        @media screen  and (min-width: 992px){

            .Acc_Login_signup_container li{
                font-size: 1.8em;
                padding: 10px;
            }
            .Acc_login_btn{
font-size: 5rem;
            }
            .Acc_signup_btn{
font-size: 5em;
            }

            .Acc_Socials_container li{
font-size: 1.5em;
margin-top: -15px;
margin: -15px 10px 0px  -15px;
padding: 10px;
background-blend-mode:darken;

            }


.blue{
    font-size: 2em;
}

        }
