.container {
    display: flex;
    cursor: pointer;
  }
  
    .bal1, .bal2, .bal3 {
    width: 5px;
    height: 5px;
    background-color: rgb(92, 125, 235);

    margin: 6px 42px;
    transition: 0.5s;
    
  }
  .bal1, .bal2, .bal3 {
    width: 5px;
    height: 5px;
    background-color: rgb(76, 161, 240);
    margin: 6px 0;
    transition: 0.5s ease;
  }
  .container:hover{
background-color: rgba(255, 255, 255, 0.199)
  }
  
  
  .b1, .b2, .b3{
  padding: 3px;
  transition: 0.5s;
  }
  
   .b1 .bal1 + .bal2 + .bal3{
   background-color: rgb(114, 137, 211);
   transition: 0.5s;
  }
  
  .b1 .bal1{
  background-color: rgb(38, 168, 243);
  transition: 0.5s;
  }
  .b1 .bal2{
  background-color: rgb(19, 178, 241);
  transition: 0.5s;
  }
  .b2 .bal1, .bal2 + .bal3{
   background-color: rgb(90, 114, 248);
   transition: 0.5s;
  }
  
  .b3 .bal1, .bal2 {
   background-color: rgb(74, 200, 209);
  }
  
  .b3 .bal1{
  background-color: rgb(51, 126, 224);
  }
  
  .b2 .bal1{
  background-color: rgb(41, 131, 184);
  
  }
  
  #change .b1 {
    -webkit-transform: rotate(-45deg) translate(11px, 5px);
    transform: rotate(-45deg) translate(11px, 5px);
    z-index: 15;
    transition: 0.5s;
 
  }
  
  #change .b2 {opacity: 0;  transition: 0.5s;}
  
  #change .b3 {
    -webkit-transform: rotate(45deg) translate(-9px, 4.4px);
    transform: rotate(45deg) translate(-9px, 4.4px);
    z-index: 9;
    transition: 0.5s;
  }


  
  #change:hover + .b1 {
    opacity: 0.9;
z-index: 9;
  cursor: pointer;
  transition: 0.5s ease;
  }