.headbtn{
  color: white;
  position: relative;
  overflow: hidden;
  background-color:#01bbb1;
width: 100px;
  text-align: center;
  align-items: center;
  cursor: pointer;
  transition: 2s;
  height: 40px;
  border-radius: 10px;
  border: none;
}


.headbtn:focus{
box-shadow: inset 5px 5px 10px -3px rgba(0,0,0,0.8);
}


.headbtn:after{
  color: white;
    content: "Call Us";
    position: absolute;
    left: 0;
    top: 0;
    bottom: -62px;
   
    transition: 1s;
    right: 0;
    width: 100px;
    /* height: 100%; */
    border-radius: 10px;
  overflow: hidden;
    z-index: 5;
    text-align: center;
    padding-top: 10px;
}

.headbtn::before{
  color: white;
    content: "";
    position: absolute;
    text-align: center;
    left: 0;
    top: 0;
    overflow: hidden;
padding-top: 0px;
    bottom: 0;
    transition: 1s;
    right: 90px;
    width: 0px;
  /*   height: 100%; */
    border-radius: 10px;
    transition: 0.5s ease;
    background-color:#00dbdb;
    
}




.headbtn:hover::before{

width: 100%

}


.headbtn:focus::before{
  box-shadow: inset 5px 5px 10px -3px rgba(0,0,0,0.8);
  }


.hbtn{
  color: white;
  position: relative;
  overflow: hidden;
  background-color:#01bbb1;
width: 100px;
height: 40px;
  text-align: center;
  border-radius: 10px;
  border:none;
  align-items: center;
  cursor: pointer;
  transition: 2s;
              
}


.hbtn:after{
  color: white;
    content: "Get Directions";
    position: absolute;
    left: 0;
    top: 0;
    bottom: -62px;
   
    transition: 1s;
    right: 0;
    width: 100px;
    /* height: 100%; */
    border-radius: 10px;
  overflow: hidden;
    z-index: 5;
    text-align: center;
    padding-top: 10px;
}

.hbtn::before{
  color: white;
    content: "";
    position: absolute;
    text-align: center;
    left: 0px;
    top: 0;
    overflow: hidden;
padding-top: 0px;
    bottom: 90px;
    transition: 1s;
    right: 0;
    width: 100px;
  /*   height: 100%; */
    border-radius: 10px;
    transition: 2s ease;
    background-color:#00ffff;
}


.hbtn:hover::after{
color: black
}

.hbtn:hover::before{

  bottom: -90px;
  color: black

}



.headbtn:focus{
  box-shadow: inset 5px 5px 10px -3px rgba(0,0,0,0.8);
  background-color:#00ffff;
color: black;
}

.hbtn:focus::before{
  box-shadow: inset 5px 5px 10px -3px rgba(0,0,0,0.8);
color: black  
}




