.container_categories{
padding: 20px;
margin-left: 0px;

}

.H2{
 color:   #519fff;
 font-size: 1.6em;
 margin-top: 0px;
 margin-bottom: 10px;
}

.H1{
   color: #101737;
font-size: 1.5rem;
font-weight: bold;
white-space: pre;

margin: auto;

}

.p{
color: #515151;
font-size: 1.5em;
font-weight: 500;
margin-bottom: -40px;
margin-left: 0px;
position: absolute;
margin-top: -5px;
}

.p_span{
color: blue;
font-size: 0.9em;

}



.p_span::before{
    content: "\279c";
    color: blue;
 
animation: blink ease 1s infinite;
    font-size: 20px;
    margin-left: 12px;
}

@keyframes blink {
 from{
   opacity: 0;
   transform: translateX(0px);
margin-left: 0px;
 }  
 to{
    transform: translate(100px);
transform: translateY(20px);
 } 
}



.box_container{
cursor: pointer;
    width: 190px;
    height: 240px;
    background-color: black;
    border-radius:17px ;
    color: white;
    padding: 80px;
    margin: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    justify-items: center;
    box-shadow: 1px 2px 7px 2px rgb(172 172 172 / 19%);
margin-left: 20px;
margin-right: 30px;
object-fit: contain;
background-position: center;
background-clip: border-box;
background-size: cover;
text-align: center;

background-repeat: no-repeat;
}

.box_container:hover{
transform: scale(1.05);
transition: 0.35s ease-in-out;
}
.box_name{
font-size: 2em;
margin-bottom: 20px;
white-space: pre;
color: white;
text-shadow: 1px 1px black;
}

.box_amount{
font-size:1.5em;
white-space: pre;
margin-bottom: 40px;
text-shadow: 2px 2px black;
color: white;
font-weight: bold;


}

.box_item{

}



