.Drawer_container{
width: 100%;
max-width: 100vw;
display: flex;

height: 100px;
margin-top: 50px;
justify-content:  space-between;
position: fixed;

z-index: 9;
}






.Submit_btn{
  display: none;

}


.Drawer_nav{
    color: black;
    padding: 20px;
    display: flex;
    flex-direction: column;
    float: left;
    position: fixed;
    left: -50px;
  bottom: 0;
  top: 0;
width: 0px;
  overflow: hidden;
  background:  #1f1e1e;
    z-index: 10;
    backdrop-filter: blur(90%);
    
}

.Drawer_open{
    font-size: 2.25em;
    cursor: pointer;
    margin-top: -45px;
    margin-left: 5px;
    z-index: 9;
    position: absolute;
}

.Drawer{
    position: fixed;
    overflow: hidden;
    background-color: rgb(255, 255, 255);
left: 0;
top: 0;
bottom: 0;
z-index: 2;
}

.Drawer_icon{
float: left;
position:relative;
top: 0;
left: -25px;
height: 50px;
z-index: 1;
}


.Li {

    text-decoration: none;
    padding: 10px;
    font-size: 1.5em;
    padding: 15px;
    color: #efefef;
    padding-left: 20px;
    color: rgb(238, 238, 238);
    text-shadow: 1.4px 1.4px   2px rgb(22, 22, 22);
    font-weight: bold;
    transition: color 0.3s ease;
    

}
.Li:hover{
   transform: scale(1.02);
   color: white;

}



.Drawer_head{
margin-top: -10px;
margin-left: 30px;
text-decoration: underline;
font-weight: bold;
background-image: -webkit-linear-gradient(left, blue,white, white);
color: transparent;
background-image: linear-gradient(left, blue,white, white);
background-image: -moz-linear-gradient(left, blue,white, white);;
-webkit-background-clip: text;
-moz-background-clip: text;
background-clip: text;
}

.Drawer_head2{
text-decoration: underline;
background-image: -webkit-linear-gradient(left,  rgb(245, 245, 252),rgb(68, 130, 189));
color: transparent;
background-image: linear-gradient(left, rgb(245, 245, 252),rgb(17, 123, 223), rgb(19, 101, 223));
background-image: -moz-linear-gradient(left,  rgb(245, 245, 252),rgb(17, 123, 223));
-webkit-background-clip: text;
-moz-background-clip: text;
background-clip: text;
}
