.D_btn{

  display: bloc;
  height: 50px;
  z-index: 5;
  justify-self: center;
  align-self: center;
  justify-content: center;
  padding-bottom: 1px;
  align-items: center;
  margin-right: 30px;
  margin-left: -0.2px;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  position: relative;;
  background-color: #0057b3;
  border: none;
 overflow-y: hidden;
display: inline-block;

position: relative;
width: 11.8rem;
text-align: center;
border-radius: 10px;
font-size: 1.2rem;
position: relative;
transition: 0.90s ease;
}

.D:after{
  content: "+";
  position: absolute;
  right:0;
  top: 25%;
  left: 85%;
  color: greenyellow;
  z-index: 3;
}
.D_btn:after{
  content: "Submit Listing";
  z-index: 2;
  position: absolute;
  left: 0;
  right: 0;
  top: 25%;
  bottom: 0;
  padding-right: 10px;
  text-align: center;
  align-self: center;
}

.D_btn:before{
  content: "";
  background-color: #0884ff;
  position: absolute;
  top: 60px;
  transition: 1s;
  bottom: 0;
  left: 0;
  right: 0;
z-index: 1;

}

.D_btn:hover::before{
top: 0;
}

.D_btn:focus::before{

box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.7);
}

.D_btn:focus{

  box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.7);
  }

