.container_Recent_listings {
    padding: 10px 0px;
  display: grid;
  justify-content: space-between;
align-items: center;
    grid-template-columns: 90%;
margin: auto;
align-items: center;
justify-content: center;
margin-bottom: 200px;
grid-auto-flow: row;
margin-bottom: 20px;
}








.main_box{

    height: fit-content;
    background-color: #f5f9ff;
    border-radius: 20px;
    padding: 0px;
    display: flex;
    flex-direction: column;
    height: fit-content;
    width: 100%;
    margin: auto;
    position: relative;
    box-shadow: 1px 2px 7px 2px rgb(172 172 172 / 19%);
}

.main_box2{
 float: left;
    height: fit-conten;
    background-color: #f5f9ff;
    padding: 0px;
    display: flex;
    margin: auto;
    border-radius: 20px;
    height: fit-conten;
    margin-top: 100px;
    position: relative;
    
    width: 100%;
margin-bottom: 50px;
    box-shadow: 1px 2px 7px 2px rgb(172 172 172 / 19%);

}

.img{
width: 100%;
border-radius: 10px;
background-attachment: fixed;
height: 50%;
max-height: fit-content;
min-height: fit-content;
margin: 0px 0px;
background-blend-mode: darken;
margin-left: 0px;
margin-right: 0px;
display: block;

border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;

animation-fill-mode: backwards;
image-rendering:optimizeQuality;
image-rendering: -webkit-optimize-contrast;
border-image: stretch;
box-shadow: inset;

}

.img2{

    height: auto;
    width:50%;
 
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    animation-fill-mode: backwards;
    image-rendering:optimizeQuality;
    image-rendering: -webkit-optimize-contrast;
    border-image: stretch;
    box-shadow: inset;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;

}

.items{

}

.headers{

width: 100%;
max-width: 100%;
position: absolut;
left: 0;
right: 0;
height: fit-contentt;



    padding-left: 10px;
    box-shadow: 0px -11px 20px -11px #f5f9ff;
}

.headers:before{
    content: "";
    position: absolut;

}

.h4{

    color: rgb(63, 60, 60);
    text-align: left;
 font-size: 1.3rem;
 margin-top: 5px;
margin-bottom: -20px;
}
.h5{
font-size: 1.1rem;
color: #519fff;
}

.p{
color: black;
background-color: #f5f9ff;

font-size: 1.5em;
padding: 25px 8px;
margin-top: -15px;
}

.icons{
font-size: 1rem;
padding: 1px 0px;
color: rgb(219, 219, 219);
margin: auto;

}

.icons span{
    margin-left: 10px;
}

.Facolor{
    color:#101737;
    padding-left: 10px;
    font-size: 0.90em;
    white-space: pre;
}

.butt{
padding: 5px;
position: relative;
transition: 1s;
overflow-y: hidden;
margin: 10px;
width: 100px;
height: 40px;
border: none;
border-radius: 10px;
color: rgb(255, 255, 255);
background-color: #12bcda; ;
cursor: pointer;
z-index: 3;
}

.butt:after{
    color: white;
      content: "View Details";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0px;
     
      transition: 1s;
      right: 0;
      width: 100px;
      /* height: 100%; */
      border-radius: 10px;
    overflow: hidden;
      z-index: 5;
      text-align: center;
      padding-top: 10px;
  }
  
  .butt::before{
    color: white;
      content: "";
      position: absolute;
      text-align: center;
      left: 0;
      top: 0;
      overflow: hidden;
  padding-top: 0px;
      bottom: 90px;
      transition: 1s;
      right: 0;
      width: 100px;
    /*   height: 100%; */
      border-radius: 10px;
      transition: 0.5s ease;
      background-color:#37d1ec;
      
  }
  
  
  
  
  .butt:hover::before{
  
 bottom: 0;
  
  }
  




.container_icons{
    position: absolute;
display: flex;
justify-content: space-between;
left: 0;
right: 0;
padding: 10px;
font-size: 1rem;
margin-top: 10px;

}
.star{
margin: 10px;
background-color: #ffa80a;
padding: 9px;
padding-top: 10px;
text-align: center;
color: #f5f9ff;
border-radius: 10px;
cursor: pointer;
}

.pending{
margin: 10px;
background-color:crimson;
border-radius: 10px;
padding: 8px;
color: #f5f9ff;

cursor: pointer;
}

.heart{
float: right;
background-color:rgb(252, 224, 224);
border-radius: 10px;
padding: 8px;
padding-top: 12px;
color:#f5f9ff;
cursor: pointer;
}

.onsale{
    margin: 10px;
    background-color:#30ca71;
    border-radius: 10px;
    padding: 8px;
    color: #f5f9ff;
    
    cursor: pointer;
    }



